body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.my_form{
    margin: auto;
    padding-top: 150px;
}

.my_input{
    margin-bottom:40px;
    padding: 10px;
    border-left: 2px solid #03009e00;
    border-right: 2px solid #03009e00;
    border-top: 2px solid #03009e00;
    border-bottom:2px solid #3AB0FF;
    border-radius: 40px;
    width: 300px;
    outline:none;
}

.my_input:focus{
    margin-bottom:40px;
    padding: 10px;
    border-left: 2px solid #3AB0FF;
    border-right: 2px solid #3AB0FF;
    border-top: 2px solid #3AB0FF;
    border-bottom:2px solid #3AB0FF;
    border-radius: 30px;
    width: 300px;
    outline:none;
}

.my_lable{
    color: #1F4690;
    font-size: large;
    font-weight: bold;
}

.resulte{
    margin-top: 30px;
    color: #1F4690;
    font-size: larger;
    font-weight: bolder;
}

.info{
    border: #3AB0FF solid 3px;
    border-radius: 30px;
    padding: 20px;
}
